import React, { Component } from 'react';

// -- json data
import CalloutImageJson from '../../static/json/map_callouts.json';

class callout_image_box extends Component {
	state = {
		current_image: 0
	};

	componentWillMount() {
		setInterval(() => {
			if (this.state.current_image === 3) {
				this.setState({ current_image: 0 });
			} else {
				this.setState({ current_image: this.state.current_image + 1 });
			}
		}, 2000);
	}

	render() {
		return (
			<div>
				<h2>test</h2>
				counter: {this.state.current_image}
			</div>
		);
	}
}

export default callout_image_box;
